@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
:root {
  --gray-900: #1a202c;
  --gray-800: #2d3748;
  --gray-700: #4a5568;
  --gray-600: #718096;
  --gray-500: #a0aec0;
  --gray-400: #cbd5e0;
  --gray-300: #e2e8f0;
  --gray-200: #edf2f7;
  --gray-100: #f7fafc;
  --red-100: #fff5f5;
  --red-200: #fed7d7;
  --red-300: #feb2b2;
  --red-400: #fc8181;
  --red-500: #f56565;
  --red-600: #e53e3e;
  --red-700: #c53030;
  --red-800: #9b2c2c;
  --red-900: #742a2a;
  --blue-100: #ebf8ff;
  --blue-200: #bee3f8;
  --blue-300: #90cdf4;
  --blue-400: #63b3ed;
  --blue-500: #4299e1;
  --blue-600: #3182ce;
  --blue-700: #2b6cb0;
  --blue-800: #2c5282;
  --blue-900: #2a4365;
  --white: white;
  --red: #f56565;
  --text-decoration-color: var(--gray-400);
  --text-color: var(--gray-800);
  --focus-ring-color: var(--blue-500);
} 
body {
    background-color: #f7f7f7;
    width:1024px;
    margin:auto;
    padding: 50px;
    font: 14px "Lucida Grande", Helvetica, Arial, sans-serif;
}
@media all and (min-width:768px) and (max-width:1024px) {
  body {
    width:90%;
    padding: 35px;
  }
}

#trends > h2 > a {
  color:black;
  text-decoration: none;
}
.contents-wrapper {
  display: flex;
  background-color: white;
  padding:30px;
  margin:5px;
  /* border: #F1F1F1 3px solid; */
  box-shadow: 5px 10px 18px#F1F1F1
}
.index {
  color:grey;
  padding-top: 2px;
  padding-right: 24px;
  font-size: 1.45em;
}
.main-contents {
  display:inline-block;
}
.title {
  font-size: 1.35em;
}
.news {
  color: #2d5ba9;
  font-size: 1.22em;
  text-decoration: none;
}
@media all and (min-width:320px) and (max-width:768px) {
  body {
    width:90%;
    padding: 10px;
  }
  .contents-wrapper {
    display: flex;
    background-color: white;
    padding: 23px 20px 12px 20px;
    margin:5px;
    /* border: #F1F1F1 3px solid; */
    box-shadow: 5px 10px 18px#F1F1F1
  }
  .index {
    color:grey;
    padding-top: 2px;
    padding-left: 1px;
    padding-right: 24px;
    font-size: 1.3em;
  }
  .title {
    font-size: 1.3em;
  }
  .news {
    color: #2d5ba9;
    font-size: 1em;
    text-decoration: none;
  }
}
@media (max-width:320px) {
  body {
    width:90%;
    padding: 10px;
  }
  .contents-wrapper {
    display: flex;
    background-color: white;
    padding:20px;
    margin:5px;
    /* border: #F1F1F1 3px solid; */
    box-shadow: 5px 10px 18px#F1F1F1
  }
  .index {
    color:grey;
    /* padding-top: 0px; */
    padding-right: 24px;
    font-size: 1.3em;
  }
  .title {
    font-size: 1.2em;
  }
  .news {
    color: #2d5ba9;
    font-size: 1em;
    text-decoration: none;
  }
}
#header {
  margin-bottom: 30px;
}
#home > a {
  font-family: 'Lobster', cursive;
  color: black;
  text-decoration: none;
  font-size: 3em;
}
.buttons {
  float: right;
  margin-right: 10px;
}

#telegram {
  display:inline-block
}
#telegram > i {
  font-size:36px;
}
#telegram > span {
  display:block;
}

#help {
  margin-right: 15px;
  display:inline-block;
  text-align: center;
}
#help > i {
  font-size:36px;
}
#help > span {
  display:block;
}

.buttons > a {
  margin-right: 15px;
  color:black;
  text-decoration: none;
  display:inline-block;
  text-align: center;
}
.buttons > a > i {
  font-size:36px;
}
.buttons > a > span {
  display:block;
}
@media all and (min-width:321px) and (max-width:768px) {
  #home > a {
    font-size: 2.3em;
  }
  #question {
    margin-right: -4px;
  }
}
@media (max-width:320px) {
  #home > a {
    font-size: 2.2em;
  }
  #telegram > i {
    font-size:26px;
  }
  .buttons {
    margin-right: 0;
  }
  #help > i {
    font-size:26px;
  }
  .buttons > a > i {
    font-size:26px;
  }
  #question {
    margin-right: 2px;
  }
}
.Modal-overlay{position:fixed;top:0;left:0;bottom:0;right:0;background-color:rgba(0,0,0,0.16)}.Modal{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);width:30%;border-radius:10px;background-color:white;box-shadow:0px 3px 6px rgba(0,0,0,0.16)}.Modal>*{margin-left:16px;margin-right:16px}.Modal>*:first-child{margin-top:16px}.Modal p.title{font-size:16pt;font-weight:bold;color:#333}.Modal .content{border-top:1px solid #bebebe;margin-top:16px}.Modal .content p{padding:8px;font-size:12pt;color:#999}.Modal .button-wrap{margin:0;margin-top:8px}.Modal .button-wrap button{width:100%;padding:12px 0;border-radius:0 0 10px 10px;background-color:#2B475E;font-size:13pt;color:white;border:0;cursor:pointer}.Modal .button-wrap button:hover{background-color:#1c2f3f}.Modal .button-wrap button:active{background-color:#1c2f3f}@media all and (min-width: 768px) and (max-width: 1024px){.Modal{width:60%}}@media all and (min-width: 320px) and (max-width: 768px){.Modal{width:80%}}@media (max-width: 320px){.Modal{width:90%}.button-wrap button{padding:0}}

label {
  display: block;
  font-size: 1.3em;
  margin-bottom: 13px; 
}
.text_input_area{
  margin-top: 1px;
  padding:10px 0px 10px 0px;
  width: 100%;
}
input {
  padding: 10px;
  width: 50%;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-weight: 300;
  max-width: 100%;
  box-sizing: border-box;
  transition: border .12s ease-in-out;
}
textarea {
  border: 1px solid #ddd;
  border-radius: 2px;
  resize: vertical;
  width: 100%;
  outline: none;
}

#submit_form   {
  margin-top: 1rem;
  padding: 0.5rem 1.25rem;
  font-size: 1rem;
  border-radius: 10px;
  background-color: var(--gray-700);
  border: 2px solid var(--gray-700);
  color: white;
  text-decoration: none;
  font-weight: bold;
  margin-bottom: 1rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  line-height: normal;
  line-height: initial;
  transition: background-color 200ms ease-in-out, border 200ms ease-in-out, transform 200ms ease-in-out;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  float: right;
}
.err_msg {
  font-size: 12px;
  color: var(--red-600);
  margin-top: 0.25rem;
}
@media all and (min-width:769px) and (max-width:1024px) {
  input {
    width:75%
  }
  textarea {
    width:86%
  }
  #submit_form   {
    margin-right: 2vw;
  }
}
@media (max-width:768px) {
  input {
    width:84%
  }
  textarea {
    width:86%
  }
  #submit_form   {
    margin-right: 5vw;
  }
}
